  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.content, .info__content {
  color: #fdfffc;
  background-color: #161927;
  border-radius: 20px;
  margin: 1.5rem;
  padding: 20px;
}

.content h1, .info__content h1 {
  text-align: center;
  margin-bottom: 5px;
  line-height: 1.1;
}

.content p, .info__content p {
  margin-top: 7px;
}

.content ul, .info__content ul, .content li, .info__content li, .content p, .info__content p, .content button, .info__content button {
  font-family: sans-serif;
}

.content button, .info__content button {
  text-transform: uppercase;
  border-radius: 10px;
  margin: 20px auto 0;
  padding: 5px 10px;
  font-size: 1.6rem;
  font-weight: bold;
  display: block;
}

.content__end {
  text-align: center;
  display: none;
}

.content__end p {
  font-size: 1.5rem;
}

.speedSnek {
  width: 100%;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.ui, .speedSnek__ui {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  display: flex;
}

.ui__speedometer {
  width: 100%;
  max-width: 400px;
  background-color: #161927;
  border: 5px solid #161927;
  border-radius: 20px;
  justify-self: end;
  margin-right: 0;
}

.score, .ui__score {
  color: #fdfffc;
  text-align: right;
  white-space: nowrap;
  background-color: #161927;
  border-radius: 20px;
  margin-right: 10px;
  padding: 8px 15px;
  font-family: monospace;
  font-size: 1.5rem;
}

@media (max-width: 640px) {
  .score, .ui__score {
    font-size: 1rem;
  }
}

.score__current, .score__best {
  justify-self: start;
